<template>
  <Container :title="title">
    <v-form @submit="submit">
      <template slot="base">
        <group :label="lang.pid" type="select" :options="rule_options" v-model="auth_rule.pid" />
        <group :label="lang.name" type="input" v-model="auth_rule.name" />
        <group :label="lang.route" type="input" v-model="auth_rule.route" />
        <group :label="lang.method" type="select" :options="method_options" v-model="auth_rule.method" />
        <group :label="lang.type" type="select" :options="type_options" v-model="auth_rule.type" />
        <group :label="lang.status" type="status" v-model="auth_rule.status" />
        <group :label="lang.sorts" type="input" v-model="auth_rule.sorts" />
      </template>
    </v-form>
  </Container>
</template>

<script>
  import {Container,VForm,Group, Checkbox} from "@/components"

  export default {
    components: {Checkbox, Group, VForm,  Container },
    data(){
      return {
        title:this.$t('auth_rule.editTitle'),
        lang:this.$t('auth_rule'),
        method_options:[
          { value: 'MENU', text: 'MENU'},
          { value:'GET', text:'GET'},
          { value:'POST', text:'POST'},
          { value:'PUT', text:'PUT'},
          { value:'DELETE', text:'DELETE'},
        ],
        type_options:[
          { value:1, text:'项目'},
          { value:2, text:'模块'},
          { value:3, text:'操作'},
        ]
      }
    },
    computed:{
      auth_rule:{
        set(value){
          this.$store.commit('user/SET_AUTH_RULE', value)
        },
        get(){
          return this.$store.state.user.auth_rule
        }
      },
      rule_options(){
        return this.$store.state.user.rule_options
      }
    },
    created(){
      const uuid = this.$route.query['id']
      this.$store.dispatch('user/ruleInitial',uuid)
    },

    methods:{
      submit(){
        this.$store.dispatch('user/editRule').then( resp =>{
          if (resp){
            this.$toast.success({
              title:'API 请求成功',
              message: resp.error
            })
          }
        })
      }
    }
  }
</script>



